import { mapActions, mapGetters } from 'vuex'

export default{
	props: {
		item: Object,
		editable: Boolean
	},
	methods: {
		...mapActions('news', ['DEL_NEW']),
		edit(){
			this.$router.push({ path: '/editor', query: { edit: this.$props.item.id } })
		},
		remove(){
			let data = {
				link: "http://test.local/app/api/news/all",
				id: this.$props.item.id
			}
			this.DEL_NEW(data)
		}
	}
}
